<template>
<div class="container-fluid px-0">
  <!-- Image Banner -->
  <Carousel></Carousel>
  <!-- Introduction -->
  <div class="container py-5 my-5">
    <Title title="Who we are"></Title>
    <!-- Description -->
    <div class="row">
      <div class="col-6" style="height:300px">
        <h1>Here We are</h1>
        <p> There's nothing to say specially </p>
      </div>
      <div class="col-6 mr-0">
		    <ImageOverlay title="Title"
                      body="What is all of it for?"
                      imgSrc="https://picsum.photos/555/500"></ImageOverlay>
      </div>
    </div>
  </div>
  <!-- Portfolio Intro -->
  <div class="container my-5 py-5">
    <Title title="Our Works"></Title>
    <!-- Cards -->
    <div class="row">
      <div class="mr-4">
        <Poster class="my-4 animatable fadeInUp"
                width="300px"
                height="300px"
                title="card1"
                content="this is content"
                body="Im really tired"
                imgsrc="https://picsum.photos/300/300"></Poster>
        <Poster class="my-4 animatable fadeInUp"
        width="300px"
        height="300px"
        title="card2"
        content="this is content"
        body="Im really tired"
        imgsrc="https://picsum.photos/300/300"></Poster>
      </div>
      <Poster class="my-4 ml-3 d-none d-md-none d-sm-none d-lg-none d-xl-block animatable fadeInUp"
              width="800px"
              height="625px"
              title="card3"
              content="this is content"
              body="Im really tired"
              imgsrc="https://picsum.photos/800/620"></Poster>    
    </div>  
  </div>

  <div class="container">
    <router-link :to="{ name: 'testpage'}" class="btn btn-sm btn-outline-light">테스트</router-link>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import Carousel from '../components/Carousel'
import Title from '../components/Title'
import Poster from '../components/Poster'
import ImageOverlay from '../components/ImageOverlay'


// Scroll Event
jQuery(function($) {
  
  // Function which adds the 'animated' class to any '.animatable' in view
  var doAnimations = function() {
    
    // Calc current offset and get all animatables
    var offset = $(window).scrollTop() + $(window).height(),
        $animatables = $('.animatable');
    
    // Unbind scroll handler if we have no animatables
    if ($animatables.length == 0) {
      $(window).off('scroll', doAnimations);
    }
    
    // Check all animatables and animate them if necessary
		$animatables.each(function(i) {
       var $animatable = $(this);
			if (($animatable.offset().top + $animatable.height() - 20) < offset) {
        $animatable.removeClass('animatable').addClass('animated');
			}
    });

	};
  
  // Hook doAnimations on scroll, and trigger a scroll
	$(window).on('scroll', doAnimations);
  $(window).trigger('scroll');

});




export default {
  name: 'HomePage',
  data () {
    return {
      test: {},
      url: "/"
    }
  },
  created: function() {
  axios.get(this.url)
      .then((res) => {
        console.log(res.data)
        this.test = res.data
      })
  },
  components: {
    Carousel,
    Title,
    Poster,
	ImageOverlay,
  }
}

</script>

<style>

.block {
  height: 5em;
  line-height: 5em;
  width: 10em;
  background: #464646;
  color: #fdfdfd;
  text-align: center;
  margin: 1em auto;
  text-shadow: 0 0 1px #333; /* so one can see fadeBgColor properly */
}

.animatable {
  
  /* initially hide animatable objects */
  visibility: hidden;
  
  /* initially pause animatable objects their animations */
  -webkit-animation-play-state: paused;   
  -moz-animation-play-state: paused;     
  -ms-animation-play-state: paused;
  -o-animation-play-state: paused;   
  animation-play-state: paused; 
}

/* show objects being animated */
.animated {
  visibility: visible;
  
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -ms-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;
  
  -webkit-animation-duration: 1s;
  -moz-animation-duration: 1s;
  -ms-animation-duration: 1s;
  -o-animation-duration: 1s;
  animation-duration: 1s;

  -webkit-animation-play-state: running;
  -moz-animation-play-state: running;
  -ms-animation-play-state: running;
  -o-animation-play-state: running;
  animation-play-state: running;
}

/* CSS Animations (extracted from http://glifo.uiparade.com/) */
@-webkit-keyframes fadeInDown {
	0% {
		opacity: 0;
		-webkit-transform: translateY(-20px);
	}	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
	}
}

@-moz-keyframes fadeInDown {
	0% {
		opacity: 0;
		-moz-transform: translateY(-20px);
	}

	100% {
		opacity: 1;
		-moz-transform: translateY(0);
	}
}

@-o-keyframes fadeInDown {
	0% {
		opacity: 0;
		-o-transform: translateY(-20px);
	}

	100% {
		opacity: 1;
		-o-transform: translateY(0);
	}
}

@keyframes fadeInDown {
	0% {
		opacity: 0;
		transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}



@-webkit-keyframes fadeIn {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

